html, body {
  height: 100%;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Make the app container span the full viewport height */
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #c70000;
  color: #fff;
}

.nav-container a {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

.nav-container img {
  width: 125px;
}

footer {
  text-align: center;
  padding: 1rem;
  background-color: #c70000;
  color: #fff;
  margin-top: auto;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensure the root element takes up full height */
}

.layout {
  display: flex;
  flex-direction: column;
  flex: 1; /* Make the layout container fill available space */
}

.content {
  flex: 1; /* This makes the content area take up the remaining space */
  padding: 20px;
}